.gblocks-component-color-picker-wrapper > .components-base-control__field {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.gblocks-component-color-picker-wrapper > .components-base-control__field .components-color-palette__item-wrapper {
  margin: 0;
}

.gblocks-component-color-picker > .components-base-control:first-child {
  margin-bottom: 0;
}
.gblocks-component-color-picker .components-color-picker__inputs-wrapper {
  min-width: auto;
}
.gblocks-component-color-picker .gblocks-component-color-picker-palette {
  padding: 16px;
  margin-top: 10px;
}
.gblocks-component-color-picker .gblocks-component-color-picker-palette .components-circular-option-picker {
  display: flex;
  flex-wrap: wrap;
}
.gblocks-component-color-picker .gblocks-component-color-picker-palette .components-circular-option-picker .components-circular-option-picker__option-wrapper {
  margin: 0 8px 8px 0;
}
.gblocks-component-color-picker .components-base-control__field,
.gblocks-component-color-picker .components-color-palette {
  margin: 0;
}
.gblocks-component-color-picker .components-color-picker {
  padding: 5px;
}
.gblocks-component-color-picker .components-color-picker__inputs-toggle {
  display: none;
}
.gblocks-component-color-picker .components-color-picker__body {
  padding-bottom: 0;
}
.gblocks-component-color-picker .components-popover__content {
  padding: 10px;
  width: 300px;
}
.gblocks-component-color-picker .components-color-picker__inputs-fields {
  margin-right: 0;
}
.gblocks-component-color-picker .components-color-clear-color {
  margin: 0 16px;
}

.gblocks-component-color-opacity {
  display: flex;
  padding: 0 16px;
  margin: 10px 0;
}
.gblocks-component-color-opacity .components-range-control {
  flex-grow: 1;
  padding-left: 5px;
}

.gblocks-color-picker-area {
  width: 28px;
  height: 28px;
}